/* SCSS Document */

@charset "utf-8";
@import "../../object/component/_color";
@import "../../object/component/_font";
@import "../../object/component/_grid";
.body {
    margin-top: 118px;
}

.o-card {
    max-width: 765px;
}

.contents--center__main {
    & .o-card {
        width: 565px;
    }
    & .o-card__header.is-mobile-visible .o-card__title{
        font-size: $font20;
    }
    & .o-card__serviceimgtitle {
        color: $lightgrey--light;
    }
    & .o-card__serviceimg {
        width: 400px;
        max-width: 100%;
    }
    & .notice {
        max-width: 600px;
        background-color: $puregrey;
        padding: 20px;
        align-items: stretch;
    }
    & .notice__row {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        &>i {
            color: $orange;
        }
        &>div,
        &>a {
            line-height: 24px;
        }
    }
    & > .o-title--lg{
        font-size: $font26;
    }         
}

////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .contents--center__main {
        & .o-card {
            width: 100%;
        }
        & .o-card__header.is-mobile-visible .o-card__title{
            font-size: $font17;
        }          
        & .o-card__serviceimgtitle {
            font-size: $font13;
        }
        
        & .o-card__serviceimg{
            padding-left: 12px;
            padding-right: 12px;
            box-sizing: border-box;
            &link{
                width: 100%;
            }
        }

        & > .o-title--lg{
            margin-top: 24px;
            font-size: $font22;
        }
    }
}

////////// Tablet //////////
@media (min-width: $grid-tablet-breakpoint) and (max-width: $grid-desktop-breakpoint - 1) {
    .contents--center__main {
        & .o-card {
            width: 100%;
        }
        & .o-card__header.is-mobile-visible .o-card__title{
            font-size: $font17;
        }    
        & .o-card__serviceimgtitle {
            font-size: $font13;
        }
        
        & .o-card__serviceimg{
            padding-left: 12px;
            padding-right: 12px;
            box-sizing: border-box;
            &link{
                width: 100%;
            }            
        }     
        
        & > .o-title--lg{
            margin-top: 24px;
            font-size: $font22;
        }   
    }
}

////////// Desktop //////////
@media (min-width: $grid-desktop-breakpoint) and (max-width: $grid-menudesktop-breakpoint - 1) {}

////////// MenuDesktop //////////
@media (min-width: $grid-menudesktop-breakpoint) and (max-width: $grid-bigdesktop-breakpoint - 1) {}

////////// BigDesktop //////////
@media (min-width: $grid-bigdesktop-breakpoint) {}
